export default {
    name: "Login",
    mounted() {
        uni.getStorage({
            key: 'userInfo',
            success: response1 => {
                uni.navigateTo({
                    url: '/pages/home/Home?user=' + response1
                });
            },
            fail: reject1 => {
                debugger;
                // doNothing...
            }
        });
    },
    methods: {
        loginIntoWechat() {
            debugger;
            uni.login({
                success: response2 => {
                    debugger;
                    uni.showLoading({
                        title: '登陆中'
                    });
                    // 获取用户信息
                    uni.getUserInfo({
                        provider: 'weixin',
                        success: response3 => {
                            debugger;
                            uni.hideLoading();
                            uni.setStorage({
                                key: 'userInfo',
                                data: response3
                            });
                        },
                        fail: reject3 => {
                            debugger;
                            uni.hideLoading();
                            uni.showToast({
                                title: '获取用户信息失败',
                                duration: 1000,
                                icon: 'none'
                            });
                        }
                    });
                },
                fail: response2 => {
                    debugger;
                    uni.showToast({
                        title: '登录失败',
                        duration: 1000,
                        icon: 'none'
                    });
                }
            });
        }
    }
}
